import React, { Component } from 'react';
import "./navbar.css";
import { connect } from 'react-redux'
import Donate from '../donate/donate';
import $ from 'jquery';
import Swal from 'sweetalert2';

class NavBar extends Component {
    state = {};
    componentDidMount() {

        $(document).ready(function () {
            $(document).click(function (event) {
                var clickover = $(event.target);
                var _opened = $(".navbar-collapse").hasClass("show");
                console.log("opened:", _opened);
                console.log("has_class:", clickover.hasClass("navbar-toggle"));

                if (_opened === true && !clickover.hasClass("navbar-toggle")) {
                    // alert();
                    $(".navbar-collapse").removeClass('show');
                }
            });
        });

        document.getElementById('about-trigger').addEventListener('click',function(event){
            event.stopPropagation();
            Swal.fire('About',
                "<div>This is a voice controlled chess game for blindfold play practice</div>" +
                "<div>Developer contact: eidiazcas@gmail.com</div>"
            );
        });

    }

    render() {
        return (
            <nav className="navbar navbar-expand-md navbar-dark ">
                <a className="navbar-brand" href="#">
                    <img src="/Chess-icon.png" 
                    width="30" 
                    height="30"
                    className="d-inline-block align-top" 
                    alt=""/>                    
                    &nbsp; V-CHESS
                    
                </a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbar-collapse" aria-controls="navbar-collapse" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbar-collapse">
                    <ul className="navbar-nav ml-auto">
                        {/* <li className="nav-item active">
                            <a className="nav-link" href="#">Home <span className="sr-only">(current)</span></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">Link</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link disabled" href="#">Disabled</a>
                        </li>
                         */}
                        <li className="nav-item">
                            <a className="nav-link" id="about-trigger">
                                About
                            </a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" id="commands-trigger">
                                Voice commands list
                            </a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link">
                            <Donate></Donate>
                            </a>
                        </li>
                    </ul>

                    <form className="form-inline my-2 my-md-0">
                    </form>
                </div>

            </nav>
        );
    }
}


const map_state_to_props = state => ({

})

const map_dispatch_to_props = dispatch => ({
});

const add_maps = connect(map_state_to_props, map_dispatch_to_props);
export default add_maps(NavBar);